<template>
  <div :class="{'flex-column': isEmpty}" :style="{'height': isEmpty? '100%' : 'auto'}">
    <div class="nav flex-vertical-center">
      <div class="flex-center"
        :class="{ 'current': submitType }"
        @click="submitType=true">
        {{$t('order-meeting.statistics.intention.submitted_orders')}}
      </div>
      <div class="flex-center"
        :class="{ 'current': !submitType }"
        @click="submitType=false">
        {{$t('order-meeting.statistics.intention.draft_orders')}}
      </div>
    </div>

    <div v-loading="loading" :class="{'flex-1 flex': isEmpty}">
      <div v-if="!isEmpty && !loading">
        <row class="total flex-vertical-center">
          <column>
            <span class="num">{{confirmOrderList.wishNumber || 0}}/{{totalCompany}}</span>
            <span style="color: #666">
              {{submitType?
               $t('order-meeting.statistics.intention.accounts_with_sbmitted_orders')
               : $t('order-meeting.statistics.intention.accounts_with_draft_orders')}}
            </span>
          </column>
          <div style="width:1px;height:27px;border-right: 1px solid #03234C;padding:0"></div>
          <column>
            <span class="num">{{confirmOrderList.orederQuantity || 0}}
              <span style="font-size:14px">{{$t('order-meeting.statistics.units')}}</span>
            </span>
            <span style="color: #666">
              {{submitType ?
              $t('order-meeting.statistics.intention.styles_with_submitted_orders')
              : $t('order-meeting.statistics.intention.styles_with_draft_orders')}}
            </span>
          </column>
        </row>
        <el-table
          :data="paginationOrderList"
          @row-click="goToOrderDetail"
          class="table">
          <el-table-column
            prop="companyName"
            :label="$t('order-meeting.statistics.intention.company')">
          </el-table-column>
          <el-table-column
            prop="gmtCreateTime"
            :label="$t('order-meeting.statistics.intention.time')">
            <template slot-scope="scope">
              {{ scope.row.gmtCreateTime | date('YYYY/MM/DD HH:mm') }}
            </template>
          </el-table-column>
          <el-table-column
            prop="buyerName"
            :label="$t('order-meeting.statistics.intention.buyer_name')">
          </el-table-column>
          <el-table-column
            prop="salesManagerName"
            :label="$t('order-meeting.statistics.intention.salesperson')">
          </el-table-column>
          <el-table-column
            prop="shoeNumber"
            :label="$t('order-meeting.statistics.intention.style_total')">
            <template slot-scope="scope">
              {{ scope.row.shoeNumber }}{{$t('order-meeting.statistics.units')}}
            </template>
          </el-table-column>
        </el-table>
        <el-pagination
          v-if="confirmOrderList.orderList.length > 10"
          layout="prev, pager, next"
          class="pagination"
          @current-change="(e)=>{currentPage = e} "
          small
          :total="confirmOrderList.orderList.length">
        </el-pagination>
      </div>
      <empty-data v-if="isEmpty && !loading"></empty-data>
    </div>
  </div>
</template>

<script>
import Vue from 'vue';
import { getConfirmOrderList } from '@/api/order-meeting';
import axios from '../../../api/axios';
import EmptyData from './EmptyData';

export default Vue.extend({
  name: 'ConfirmIntention',
  props: {
    workspaceId: {
      default() {
        return this.$route.query.workspaceId;
      },
    },
  },
  components: {
    EmptyData,
  },
  data() {
    return {
      confirmOrderList: { orderList: [] },
      totalCompany: null,
      // 是否已下单
      submitType: undefined,
      loading: false,
      currentPage: 1,
      // 数据为空
      isEmpty: false,
    };
  },
  created() {
    if (Object.hasOwnProperty.call(this.$route.query, 'submitType')) {
      this.submitType = this.$route.query.submitType !== 'false';
    } else {
      this.submitType = true;
    }
  },
  methods: {
    async getConfirmOrderList() {
      // 重置数据
      this.loading = true;
      this.isEmpty = false;
      this.confirmOrderList = { orderList: [] };

      try {
        this.confirmOrderList = (await getConfirmOrderList(+this.submitType, this.workspaceId)).data;
      } catch (e) {
        this.$errorHandler(e);
        this.loading = false;
        this.isEmpty = true;
      }
      if (this.confirmOrderList.orderList.length === 0) {
        console.info(this.confirmOrderList.orderList.length === 0);
        this.loading = false;
        this.isEmpty = true;
      }

      // 该账号下的虚拟账号（此页面只需获取一次）
      if (this.totalCompany) {
        this.loading = false;
        return;
      }
      if (this.confirmOrderList.supervisor) {
        this.totalCompany = (await axios({
          method: 'get',
          url: '/authcenter/user/company/virtual/count',
        })).data;
      } else {
        this.totalCompany = (await axios({
          method: 'get',
          url: '/authcenter/user/created/virtual/count',
        })).data;
      }

      this.loading = false;
    },


    goToOrderDetail(row) {
      this.$router.push({
        path: 'order-detail',
        query: {
          orderId: row.orderId,
          userId: row.userId,
          submitType: this.submitType,
          workspaceId: this.$route.query.workspaceId,
        },
      });
    },
  },

  watch: {
    submitType(val) {
      this.$router.push({
        path: this.$route.path,
        query: {
          ...this.$route.query,
          submitType: val,
        },
      });
      this.currentPage = 1;
      this.getConfirmOrderList();
    },
  },
  computed: {
    paginationOrderList() {
      return this.confirmOrderList.orderList.slice(
        this.currentPage * 10 - 10, this.currentPage * 10,
      );
    },
  },


});
</script>

